import { ThumbUpAlt, ThumbUpOffAlt } from '@mui/icons-material'
import ShareIcon from '@mui/icons-material/Share'
import {
    Card,
    CardActionArea,
    CardActions,
    CardContent,
    CardMedia,
    Checkbox,
    IconButton,
    Skeleton,
    Typography,
} from '@mui/material'
import React from 'react'
import { useSelector } from 'react-redux'

import thumbsUp from './sounds/thumbsUp.mp3'

export const MaterialCard = ({title, uploadedBy}) => {
    const appProgress = useSelector(state => state.app.progress)
    const handleThumbsUp = () => {
        const sound = new Audio(thumbsUp)
        sound.play()
    }
    return (
        <Card sx={{maxWidth: 345}} elevation={10}>
            <CardActionArea>
                {
                    appProgress ?
                      <Skeleton
                        variant="rectangular"
                        animation="wave"
                        width="auto"
                        height={140}
                      /> :
                      <CardMedia
                        component="img"
                        height="140"
                        image="https://i.pravatar.cc/300"
                        alt="green iguana"
                      />
                }
                <CardContent>
                    <Typography noWrap gutterBottom variant="h6" component="div">
                        {
                            appProgress ?
                          <Skeleton
                            animation="wave"
                          /> : title
                        }
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                        {
                            appProgress ?
                              <Skeleton
                                animation="wave"
                              /> : `By ${uploadedBy}`
                        }
                    </Typography>
                </CardContent>
            </CardActionArea>
            <CardActions disableSpacing>
                {/* <IconButton aria-label="add to favorites">
            <FavoriteIcon />
          </IconButton> */}
                {
                    appProgress ?
                      <Skeleton
                        variant="circular"
                        width={40}
                        height={40}
                      /> : <Checkbox
                        onClick={handleThumbsUp}
                        icon={<ThumbUpOffAlt/>}
                        checkedIcon={<ThumbUpAlt sx={{ color: '#04a2e0' }}/>}
                      />
                }
                {
                    appProgress ?
                      <Skeleton
                        sx={{marginLeft: 2}}
                        variant="circular"
                        width={40}
                        height={40}
                      /> : <IconButton aria-label="share">
                          <ShareIcon/>
                      </IconButton>
                }
            </CardActions>
        </Card>
    );
};
