import { SET_APP_PROGRESS, UNSET_APP_PROGRESS } from './types'
import ruetkitApi from '../apis/ruetkitApi'

export const addMaterialMeta = ({data, misc}) => async (dispatch, getState) => {
  try {
    dispatch({type: SET_APP_PROGRESS})
    const accessToken = localStorage.getItem('accessToken')

    const addMaterialMeta = await ruetkitApi.post('/api/materials', data, {
      headers: {
        Authorization: `Bearer ${accessToken}`
      }
    })
    console.log(addMaterialMeta.data)
    dispatch({type: UNSET_APP_PROGRESS})
  } catch (err) {
    if (err.response.data.error.field === 'title') {
      misc.setTitleError(err.response.data.error.detail)
    }
  } finally {

  }
}