import { SET_APP_PROGRESS, SET_DEPARTMENTS, SET_MATERIALS, SET_NAVIGATION, UNSET_APP_PROGRESS } from '../actions/types'


export const ruetKitAppReducer = (initialState={navigate: "/"}, action) => {
    switch (action.type) {
        case SET_APP_PROGRESS:
            return {...initialState, progress: true}
        case UNSET_APP_PROGRESS:
            return {...initialState, progress: false}
        case SET_NAVIGATION:
            return {...initialState, navigate: action.payload}
        case SET_MATERIALS:
            return {...initialState, materials: action.payload}
        case SET_DEPARTMENTS:
            return {...initialState, departments: action.payload}
        default:
            return initialState
    }
}