import {
  Autocomplete, Button,
  Divider,
  FormControl,
  InputAdornment,
  LinearProgress,
  Stack,
  TextField,
  Typography, useTheme,
} from '@mui/material'

import { Box } from '@mui/system'
import React, { useEffect, useState } from 'react'
// import { Editor } from "react-draft-wysiwyg";
// import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import { CorporateFare, Login, MenuBook, Title, Upload } from '@mui/icons-material'

// import {Editor, EditorState} from 'draft-js';
// import 'draft-js/dist/Draft.css';
import ClassIcon from '@mui/icons-material/Class';
import ReactQuill from 'react-quill' // ES6
import { useDispatch, useSelector } from 'react-redux'
import { fetchDepartments } from '../../actions/fetchDepartments'
import { fetchCourses } from '../../actions/fetchCourses'
import { toast } from 'react-toastify'
import { addMaterialMeta } from '../../actions/addMaterialMeta'
import { UNSET_APP_PROGRESS } from '../../actions/types'

export const AddMaterial = () => {
  const dispatch = useDispatch()
  const departments = useSelector(state => state.app.departments)

  const [departmentOptions, setDepartmentOptions] = useState([])
  const [showDepartmentOptionsProgress, setShowDepartmentOptionsProgress] = useState(true)

  const [courseOptions, setCourseOptions] = useState([])
  const [showCourseOptionsProgress, setShowCourseOptionProgress] = useState(true)

  const [selectedDepartment, setSelectedDepartment] = useState()
  const [selectedCourse, setSelectedCourse] = useState()

  const [title, setTitle] = useState('')
  const [description, setDescription] = useState({ text: '' })

  const [titleError, setTitleError] = useState('')
  const [descriptionError, setDescriptionError] = useState()

  useEffect(() => {
    console.log(selectedDepartment)
  }, [selectedDepartment])

  useEffect(() => {
    console.log(selectedCourse)
  }, [selectedCourse])

  useEffect(() => {
    if (departments !== undefined) {
      const _departmentOptions = []
      departments.map((department, _index) => {
        _departmentOptions.push({ label: `${department.description} [${department.acronym}]`, id: department.id })
      })
      setDepartmentOptions(_departmentOptions)
    }
  }, [departments])


  useEffect(() => {
    if (selectedDepartment !== null && selectedDepartment !== undefined) {
      setSelectedCourse()

      setShowCourseOptionProgress(true)
      const data = {
        departmentId: selectedDepartment.id
      }
      const misc = {
        setShowCourseOptionProgress,
        setCourseOptions
      }
      dispatch(fetchCourses({data, misc}))
    }
  }, [selectedDepartment])


  useEffect(() => {
    console.log(departmentOptions)
    dispatch({type: UNSET_APP_PROGRESS})
    if (departments === undefined) {
      const misc = {
        setShowDepartmentOptionsProgress
      }
      dispatch(fetchDepartments({ misc }))
    } else {
      setShowDepartmentOptionsProgress(false)
    }
  }, [])

  const handleEditor = (value) => {
    setDescription({ text: value })
    console.log(value)
  }

  const onClickCourse = () => {
    if (selectedDepartment === undefined) {
      toast.warn('Please specify a department', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  }

  const handleUpload = () => {
    // TODO check if form is valid

    const data = {
      title,
      description: description.text,
      course_id: selectedCourse.id
    }
    const misc = {
      setTitleError
    }
    dispatch(addMaterialMeta({data, misc}))
  }
  const theme = useTheme()
  useEffect(() => {
    if (theme.palette.mode === 'dark') {
      console.log('dark')
      document.querySelector('.ql-editor').style.background = '#121212'
    } else {
      document.querySelector('.ql-editor').style.background = '#fff'
    }
  }, [theme])
  const placeholderStyle = '.quill > .ql-container > .ql-editor.ql-blank::before{\n' +
    '    color: white;\n' +
    '}'
  return (
    <Box flex={2} p={2}>
      {useTheme().palette.mode === 'dark' && <style>{placeholderStyle}</style>}
      <Stack justifyContent="space-between" width="100%">
        <Typography color="gray" variant="h5" sx={{ fontWeight: 500, m: 1 }}>
          Add a new material
        </Typography>
        <Divider/>
        {/*<Paper sx={{mt: 2}} elevation={1}>*/}
        <div>
          <FormControl>
            <TextField
              // disabled={open ? true : false}
              type="text"
              error={titleError.length == 0 ? false : true}
              helperText={titleError}
              value={title}
              // onChange={}
              sx={{ mt: 2, width: { xs: '50ch', sm: '65ch' } }}
              size="small"
              label="Title"
              variant="outlined"
              placeholder="What is your material title?"
              onChange={(event)=>setTitle(event.target.value)}
              InputProps={{
                startAdornment:
                  <InputAdornment position="start">
                    <Title/>
                  </InputAdornment>
              }}/>
          </FormControl>
        </div>
        <div>
          <FormControl sx={{ width: { xs: '50ch', sm: '65ch' }, mt: 2 }}>
            <ReactQuill
              onChange={handleEditor}
              style={{ border: '1px solid #c2c2c2', borderRadius: 2}}
              placeholder="Type in a fancy description..."
              modules={{
                toolbar: [
                  [{ 'header': [1, 2, 3, 4, 5, false] }],
                  ['bold', 'italic', 'underline', 'strike', 'blockquote'],
                  // [{'align': 'center'}],
                  [{ 'list': 'ordered' }, { 'list': 'bullet' }, { 'indent': '-1' }, { 'indent': '+1' }],
                  [{'color': []}, {'background': []}],
                  ['link'],
                  ['clean']
                ],
              }}
              theme="bubble"/>
            <Typography align="right" variant="caption" color="gray">Select text for more options</Typography>
            <div style={{textAlign: 'center'}} dangerouslySetInnerHTML={{ __html: description.text }}></div>
          </FormControl>
        </div>
        <div>
          <FormControl sx={{ mt: 2 }}>
            <Autocomplete
              size="small"
              disabled={showDepartmentOptionsProgress}
              sx={{ width: 400 }}
              renderInput={
                (params) =>
                  <TextField {...params} label="Department" placeholder="Select department" InputProps={{
                    ...params.InputProps,
                    startAdornment:
                      <InputAdornment position="start">
                        <CorporateFare/>
                      </InputAdornment>
                  }}/>
              }
              options={departmentOptions}
              onChange={(event, value) => setSelectedDepartment(value)}
            />
            <Box sx={{ height: 2 }}>
              {showDepartmentOptionsProgress && <LinearProgress color="secondary"/>}
            </Box>
          </FormControl>
        </div>
        <div>
          <FormControl sx={{ mt: 2 }} onClick={onClickCourse}>
            <Autocomplete
              sx={{width: 400}}
              size="small"
              disabled={showCourseOptionsProgress}
              renderInput={
                (params) =>
                  <TextField helperText="Select department to get courses" {...params} label="Course" placeholder="Select course" InputProps={{
                    ...params.InputProps,
                    startAdornment:
                      <InputAdornment position="start">
                        <ClassIcon/>
                      </InputAdornment>
                  }}/>
              }
              options={courseOptions}
              onChange={(event, value) => setSelectedCourse(value)}
            />
            <Box sx={{ height: 2 }}>
              {selectedDepartment !== undefined && showCourseOptionsProgress && <LinearProgress color="secondary"/>}
            </Box>
          </FormControl>
        </div>
        <div style={{marginTop: 5}}>
          <Button type="submit" variant="contained" color="primary" startIcon={<Upload/>} onClick={handleUpload}>Upload</Button>
        </div>
        {/*</Paper>*/}
      </Stack>
    </Box>
  )
}
